const STORAGELIST = require("../../src/constants.js");
const checkKeyRepeat = require("./check/checkKeyRepeat");
const createLocalStorage = require("./localStorage");

// 处理数据
/******************************************
 * 传入数据
 * const STORAGELIST = [
        {
           key: "token",
           fromPage: "首页",
           use: "用户校验",
           des: "首页-登录后-用户校验",
        },
    ]
 * 转化后数据
 * const STORAGELIST = {
        "token": {
           key: "token",
           fromPage: "首页",
           use: "用户校验",
           des: "首页-登录后-用户校验",
        },
    }
 * *****************************************/
function handleStorageList(storageList) {
    return storageList.reduce((prev, cur) => {
        const key = cur.key;
        // 检查配置的列表key是否重复
        checkKeyRepeat(prev, key)

        prev[key] = cur;
        return prev;
    }, {});
}

let finalyStorageList = handleStorageList(STORAGELIST);

module.exports = createLocalStorage(finalyStorageList);
